
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import axios from "axios"
export default {
name: 'JokerIndex',

data () {
return {
msg: 'aa',
index01:0,
menus: {
menu1: '分享',
menu2:"跳转"
},
showMenus: false,
type: '4',
list: [],
list1:[],
list2:[],
loadData:true,
loadData1:true,
loadData2:true,
pages:1,
pages1:1,
pages2:1,
show1: false,
text1:"加载中..."
}

},
methods:{
menu1:(key,item)=>{
console.log(key,item);
location.href="http://lininn.cn"
},
switchTabItem(index){
console.log('on-before-index-change', index)
console.log(this.index01);
this.index01 = index;
if(index==1){
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"image":1}}).then((res)=>{
var res=res.data;
this.show1=false;
var arr=[];
var reg1=/\".*\"/;
var reg=/data-src=\".*?\"/;
for(var i=0;i<res.length;i++){
var str="";
var str=res[i].content.match(reg)[0];
var str1="http"+str.substring(15,(str.length-1));
console.log("str1"+str1);

arr.push({
title:res[i].title,
content:"<img src='"+str1+"'>"
});

}
this.list1=arr;
});

}else if(index==2){
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"gif":1}}).then((res)=>{
var res=res.data;
this.show1=false;
var arr=[];
var reg1=/\".*\"/;
var reg=/data-src=\".*?\"/;
for(var i=0;i<res.length;i++){
var str="";
var str=res[i].content.match(reg)[0];
var str1="http"+str.substring(15,(str.length-1));
console.log("str1"+str1);

arr.push({
title:res[i].title,
content:"<img src='"+str1+"5'>"
});

}
this.list2=arr;
});
}
},
moreData(){
var _this=this;
if(this.loadData){
this.loadData=false;
this.pages=this.pages+1;
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"page":this.pages}}).then((res)=>{
this.show1=false;
var res=res.data;
var arr1=this.list;
this.list=[...arr1,...res];
this.loadData=true;
});
}
},
moreData1(){
var _this=this;
if(this.loadData1){
this.loadData1=false;
this.pages1=this.pages1+1;
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"image":this.pages1}}).then((res)=>{
this.loadData1=true;
this.show1=false;
var res=res.data;
var arr=[];
var reg1=/\".*\"/;
var reg=/data-src=\".*?\"/;
for(var i=0;i<res.length;i++){
var str="";
var str=res[i].content.match(reg)[0];
var str1="http"+str.substring(15,(str.length-1));
console.log("str1"+str1);

arr.push({
title:res[i].title,
content:"<img src='"+str1+"'>"
});

}
var arr1=this.list1;
this.list1=[...arr1,...arr];
});
}
},
moreData2(){
var _this=this;
if(this.loadData2){
this.loadData2=false;
this.pages2=this.pages2+1;
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"gif":this.pages2}}).then((res)=>{
this.loadData2=true;
this.show1=false;

var res=res.data;
var arr=[];
var reg1=/\".*\"/;
var reg=/data-src=\".*?\"/;
for(var i=0;i<res.length;i++){
var str="";
var str=res[i].content.match(reg)[0];
var str1="http"+str.substring(15,(str.length-1));
console.log("str1"+str1);

arr.push({
title:res[i].title,
content:"<img src='"+str1+"5'>"
});

}
var arr1=this.list2;
this.list2=[...arr1,...arr];
});
}
}
},
mounted:function(){
let _this = this;

let sw = true;
this.show1=true;
axios.get("http://hanxing33.top/api",{params:{"page":1}}).then((res)=>{
this.show1=false;
var res=res.data;
this.list=res;
console.log(this.list);

});

window.addEventListener('scroll',function(){

if(document.body.scrollTop + window.innerHeight >= document.body.offsetHeight) {


if(sw==true){

sw = false;
axios.get("http://img.lovelining.com/api",{params:{"page":2}}).then((res)=>{
console.log(res);
var res=res.data;
var arr=[];
for(var i=0;i<res.length;i++){
arr.push({
title:res[i].title,
desc:res[i].content
});
}
var arr1=this.list;
this.list=[...arr1,...arr];
console.log(this.list);
console.log(arr);
});

}
}
});


},
components:{
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
Tab,
TabItem,
Sticky,
Divider,
XButton,
Swiper,
SwiperItem,
Panel, Group, Radio,
Divider,
Card,
Loading
},
directives: {
TransferDom
}
}



