import Vue from 'vue'
import Router from 'vue-router'
import JokerIndex from '@/components/JokerIndex'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'JokerIndex',
      component: JokerIndex
    }
  ]
})



